#game-klavogonki {

	overflow: hidden;

	* {
		box-sizing: border-box;
	}

}
#game-klavogonki {
	position: relative;
}

@media (min-width: 681px) {
	._game-klavogonki_mobile-only {
		display: none;
	}
}

@media (max-width: 680px) {
	._game-klavogonki_desktop-only {
		display: none;
	}
}

.game-klavogonki-screen {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;

	visibility: hidden;
	opacity: 0.0;
	transition: opacity 300ms ease, visibility 300ms step-end;
	&._visible {
		visibility: visible;
		opacity: 1.0;
		transition: opacity 300ms ease, visibility 300ms step-start;
	}
}


.game-klavogonki-main {
	display: flex;
	flex-direction: column;
}

.game-klavogonki-visualization {
	flex: 1 0 0;
}

.game-klavogonki-timer {
	position: absolute;
	top: 20px;
	left: 50%;
	transform: translateX(-50%);
	background-color: #5f19aa;
	color: #fff;
	border-radius: 18px;
	min-width: 135px;
	height: 36px;
	line-height: 36px;
	letter-spacing: 0.5px;
	padding: 0 24px;
	font-family: 'Roboto Condensed';
	font-size: 25px;
	font-weight: bold;
	font-stretch: condensed;
	text-align: left;
	user-select: none;
	&._hidden {
		display: none;
	}
}

.game-klavogonki-logo {
	position: absolute;
	top: 20px;
	background-size: contain;

	&._hidden {
		display: none;
	}

	&._megafon {
		left: 20px;
		width: 128px;
		height: 23px;
		background-image: url('images/logo-megafon.png');
	}

	&._tj {
		right: 20px;
		width: 35px;
		height: 37px;
		background-image: url('images/logo-tj.png');
	}

}





@media (max-width: 680px) {
	.game-klavogonki-logo {
		display: none;
	}
	.game-klavogonki-timer {
		min-width: 186px;
		top: 25px;
		border-radius: 24px;
		height: 48px;
		line-height: 48px;
		padding: 0 20px;
		font-family: 'GTWalsheimPro', sans-serif;
		font-size: 31px;
		background-color: #731982;
	}
}
