$text-margin: 120px;
$text-margin-mobile: 26px;

.game-klavogonki-typing {
	font-family: 'Roboto Condensed', sans-serif;
	font-size: 22px;

	flex: 0 0 119px;
	width: 100%;
	padding: 16px $text-margin;

	background-color: #00be73;

	&._hidden {
		* {
			opacity: 0;
			transition: opacity 300ms ease;
		}
		.game-klavogonki-typing_text-wrapper {
			&::before, &::after {
				display: none;
			}
		}
		background-color: #dceef4;
		transition: background-color 300ms ease;
	}
}

.game-klavogonki-typing_text-wrapper {
	width: 100%;
	height: 40px;
	position: relative;

	&::before, &::after {
		top: 0;
		content: '';
		position: absolute;
		width: $text-margin;
		height: 100%;
		z-index: 1;
	}

	&::before {
		right: 100%;
		background: linear-gradient(to left, rgba(0, 190, 115, 0), rgba(0, 190, 115, 1) 50%, rgba(0, 190, 115, 1));
	}

	&::after {
		left: 100%;
		background: linear-gradient(to right, rgba(0, 190, 115, 0), rgba(0, 190, 115, 1) 50%, rgba(0, 190, 115, 1));
	}

}

.game-klavogonki-typing_text-window {
	height: 30px;
	line-height: 38px;
}

.game-klavogonki-typing_text {
	position: relative;
	white-space: nowrap;
	color: #00804d;
	display: inline-block;
	user-select: none;

	transition: left 100ms ease;

	span {
		transition: color 100ms ease;

		&._active {
			color: #fff;
		}
	}

	opacity: 1;
	transition: opacity 100ms ease;
	&._hidden {
		opacity: 0;
		transition: none;
	}

}

div.game-klavogonki-typing_input-window {
	display: flex;
	margin-top: 2px;
	position: relative;

	&._error::before {
		content: '';
		position: absolute;
		display: block;
		top: 12px;
		left: -29px;
		width: 18px;
		height: 18px;
		background: url(/images/icon-word-wrong.png);
		background-size: contain;
		z-index: 1;
	}

	&._error .game-klavogonki-typing_input{
		background-color: #c8355b;
		color: #000;
	}

}


.game-klavogonki-typing_input {
	flex: 1 0 0;
	height: 40px;
	border-radius: 20px;
	border: none;
	outline: none;
	padding: 7px 20px 5px;
	font-size: 25px;
	font-stretch: condensed;
}

.game-klavogonki-typing_speed {
	font-size: 18.5px;
	line-height: 40px;
	font-weight: bold;
	font-stretch: condensed;
	color: #5f19aa;
	text-transform: uppercase;
	user-select: none;
	white-space: nowrap;
}

.game-klavogonki-typing_speed-value {
	margin-left: 11px;
	font-size: 25px;
	line-height: 40px;
	font-weight: bold;
	font-stretch: condensed;
	color: #5f19aa;
}

.game-klavogonki-typing_text-window, .game-klavogonki-typing_input-window {
	max-width: 495px;
	margin: auto;
}










@media (max-width: 680px) {

	.game-klavogonki-typing {
		font-family: 'GTWalsheimPro', sans-serif;
		padding: 10px $text-margin-mobile;
	}

	.game-klavogonki-typing_text-wrapper {
		width: 100%;
		height: 40px;
		position: relative;

		&::before, &::after {
			display: none;
		}

	}

	.game-klavogonki-typing_speed {
		display: none;
	}

	div.game-klavogonki-typing_input-window {
		margin-top: 9px;
		&._error::before {
			display: none;
		}
	}

	.game-klavogonki-typing_input {
		font-family: 'GTWalsheimPro', sans-serif;
		font-size: 22px;
		padding: 0px 13px 0px;
	}
}

.game-klavogonki-visualization {
  font-family: 'Roboto Condensed', sans-serif;

  width: 100%;
  height: 100%;

  overflow: hidden;
  position: relative;
}

.game-klavogonki-visualization_town {
  // width: 50000px;
  width: 6500vh;
  height: 100%;
  min-width: 100%;

  background: url("images/bg.jpg") center center;
  position: absolute;
  z-index: -100;

  left: 0;
  transition: left 2s ease;
}


.game-klavogonki-visualization_town-background {
  height: 100%;
  width: 100%;

  left: 0;
  bottom: 0;
  position: absolute;
}

.game-klavogonki-visualization_town-background-buildings {
  height: 100%;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: flex-end;

  bottom: 0;
  left: 0;
  position: absolute;

  transition: left 2s ease;
}


.game-klavogonki-visualization_town-background-clouds {
  width: 100%;
  height: 100px;
  background: url("images/clouds.png") repeat-x bottom;
  background-size: contain;

  bottom: 0;
  left: 0;
  position: absolute;

  transition: left 2s ease;
}

.game-klavogonki-visualization_town_started {
  left: 0;
  right: unset;
}

.game-klavogonki-visualization_town_finished {
  left: unset;
  right: 0;
}

.game-klavogonki-visualization_town-ships {
  width: 100%;
  height: 100%;
  padding: 80px 0 40px;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

  position: absolute;
  left: 3vw;
  bottom: 0;

  &._hidden {
    opacity: 0;
    transition: opacity 300ms ease;
  }
}

.game-klavogonki-visualization_ship-container  {
  height: 100px;
  width: 150px;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  position: relative;

  transition: width 2s ease;
}

.game-klavogonki-visualization_ship-dash-start {
  width: 10px;
  height: 10px;

  background: white;
  border-radius: 50%;
  bottom: 48px;

  position: absolute;
  left: 0;
}

.game-klavogonki-visualization_ship-dash {
  width: calc(100% - 160px);
  height: 2px;
  background: url("images/rectangle.svg") repeat-x center left;
  background-size: contain;

  margin-bottom: 6px;
}

.game-klavogonki-visualization_ship {
  height: 100px;

  align-self: flex-end;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.game-klavogonki-visualization_ship-name {
  width: 150px;
  height: 20px;
  position: relative;
  text-transform: uppercase;
}

.game-klavogonki-visualization_ship-name > span {
  position: absolute;
  text-align: center;
  transform: translateX(-50%);
  left: 50%;
  white-space: nowrap;
  color: white;
  font-weight: bold;
  font-size: 11px;
}

.game-klavogonki-visualization_ship_main .game-klavogonki-visualization_ship-img {
  background: url("images/hovercraft_main.png") no-repeat center center;
  background-size: contain;
}

.game-klavogonki-visualization_ship-img {
  width: 150px;
  height: 50px;
  background: url("images/hovercraft_enemy.png") no-repeat center center;
  background-size: contain;
  position: relative;
}

.game-klavogonki-visualization_ship-air {
  position: absolute;
  top: 20px;
  right: 115px;
  width: 150px;
  height: 50px;
  opacity: 0.0;
  transition: opacity 2s ease;
  background: url("images/hovercraft_trace.png") no-repeat left center;
  background-size: contain;
}

._flying .game-klavogonki-visualization_ship-air {
  opacity: 1.0;
  transition: opacity 0.1s ease;
}

.game-klavogonki-visualization_ship-flame {
  position: absolute;
  top: 34px;
  right: 144px;
  width: 40px;
  height: 25px;
  opacity: 0.0;
  transition: opacity 2s cubic-bezier(0.89, 0.08, 0.76, 0.26) 0.05s;
  background: url("images/hovercraft_flame.png") no-repeat left center;
  background-size: contain;

  animation: flaming 500ms infinite ease-in-out;
}

._boosted .game-klavogonki-visualization_ship-flame{
  opacity: 1.0;
  transition: none;
}

@keyframes flaming {
  0%   { transform: scale(0.7); }
  20% { transform: scale(0.9); }
  40% { transform: scale(0.7); }
  60% { transform: scale(0.9); }
  80% { transform: scale(0.7); }
  100% { transform: scale(0.9); }
}